var aText = new Array(
  { text: 'Кряжев Олег', tag: 'h2' },
  { text: 'Frontend-разрабочик', tag: 'h4' },
  { text: 'Добро пожаловать на мой сайт!', tag: 'p' }
);
var iSpeed = 100; // time delay of print out
var iIndex = 0; // start printing array at this posision
var iArrLength = aText[0].text.length; // the length of the text array
var iScrollAt = 20; // start scrolling up at this many lines

var iTextPos = 0; // initialise text position
var sContents = ''; // initialise contents variable
var iRow; // initialise current row

export const typewriter = () => {
  setTimeout(() => {
    sContents = ' ';
    iRow = Math.max(0, iIndex - iScrollAt);
    var destination = document.getElementById(`typedtext-${aText[iIndex].tag}`);

    while (iRow < iIndex) {
      sContents += aText[iRow++].text + '<br />';
      sContents = '';
    }
    destination.innerHTML =
      sContents + aText[iIndex].text.substring(0, iTextPos);
    if (iTextPos++ == iArrLength) {
      iTextPos = 0;
      iIndex++;
      if (iIndex != aText.length) {
        iArrLength = aText[iIndex].text.length;
        typewriter();
      }
    } else {
      typewriter();
    }
  }, iSpeed);
};
