import React from 'react';

export default class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: '',
    };
  }

  render() {
    const { status } = this.state;
    return (
      <div className="contacts-form text-center">
        <form
          onSubmit={this.submitForm}
          action="https://formspree.io/f/xwkwajoa"
          method="POST"
          className="contact-form"
        >
          <h4>Свяжитесь со мной</h4>
          <div className="mb-3">
            <input
              type="name"
              className="form-control"
              id="contact-form-inputName"
              aria-describedby="nameHelp"
              name="name"
              placeholder="Ваше имя"
            />
          </div>
          <div className="mb-3">
            <input
              type="email"
              className="form-control"
              id="contact-form-inputEmail"
              aria-describedby="emailHelp"
              name="email"
              placeholder="Ваша почта"
            />
          </div>
          <div className="mb-3">
            <textarea
              rows="4"
              type="text"
              className="form-control"
              id="contact-form-inputMessage"
              aria-describedby="messageHelp"
              name="message"
              placeholder="Сообщение"
            />
          </div>
          <input type="text" name="_gotcha" style={{ display: 'none' }} />
          {status === 'SUCCESS' ? (
            <p>Спасибо!</p>
          ) : (
            <button type="submit" className="btn btn-secondary">
              Оставить сообщение
            </button>
          )}
          {status === 'ERROR' && <p>Вот незадача, что-то пошло не так </p>}
        </form>
      </div>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader('Accept', 'application/json');
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: 'SUCCESS' });
      } else {
        this.setState({ status: 'ERROR' });
      }
    };
    xhr.send(data);
  }
}
