import React from 'react';
import { ButtonGroup } from 'react-bootstrap';
import { crollToElemetHandler } from '../js/crollToElemetHandler';
import { typewriter } from '../js/typewriter';
import itMan from '../pictures/it-man.png';

// set up text to print, each item in array is new line

class Home extends React.Component {
  componentDidMount() {
    typewriter();
  }

  render() {
    return (
      <section id="home-page" className="home ">
        <div className="row">
          <div className="col-md-10 offset-md-1 d-flex justify-content-center">
            <div className="greetings">
              <h2 id="typedtext-h2"></h2>
              <h4 id="typedtext-h4"></h4>
              <p id="typedtext-p"></p>

              <ButtonGroup id="nav-buttons-home">
                <a
                  to="/about"
                  onClick={crollToElemetHandler('about')}
                  className="btn btn-secondary mx-2"
                >
                  Навыки
                </a>

                <a
                  to="/portfolio"
                  onClick={crollToElemetHandler('portfolio')}
                  className="btn btn-secondary mx-2"
                >
                  Портфолио
                </a>

                <a
                  to="/contacts"
                  onClick={crollToElemetHandler('contacts')}
                  className="btn btn-secondary mx-2"
                >
                  Контакты
                </a>
              </ButtonGroup>
            </div>
            <img className="home-logo" src={itMan} alt="" />
          </div>
        </div>
        <div className="next-page">
          <a
            to="/about"
            onClick={crollToElemetHandler('about')}
            className="next-page-btn"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="50"
              height="50"
              fill="currentColor"
              className="bi bi-arrow-down-circle-fill"
              viewBox="0 0 16 16"
            >
              <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v5.793L5.354 8.146a.5.5 0 1 0-.708.708l3 3a.5.5 0 0 0 .708 0l3-3a.5.5 0 0 0-.708-.708L8.5 10.293V4.5z" />
            </svg>
          </a>
        </div>
      </section>
    );
  }
}

export default Home;
