import React from 'react';
import { useForm } from '@formspree/react';
import ContactForm from '../components/ContactForm';

export default function Contacts() {
  const [state, handleSubmit] = useForm('contactForm');
  if (state.succeeded) {
    return <p>Thanks for joining!</p>;
  }
  return (
    <section id="contacts-page" className="">
      <h2>Контакты</h2>
      <div className="contacts-block row">
        <div className="col-md-5 col-lg-4 offset-md-0 offset-lg-2">
          <div className="contact-block">
            <h4>Телефон</h4>
            <p>+7-909-666-95-30</p>
          </div>
          <div className="contact-block">
            <h4>Почта</h4>
            <p>olegkrieg@gmail.com</p>
          </div>
          <div className="contact-block">
            <h4>GitHub</h4>
            <p>https://github.com/krieg1234</p>
          </div>
        </div>
        <div className="col-md-5 col-lg-3 offset-md-1">
          <ContactForm />
        </div>
      </div>
    </section>
  );
}
